/*
 * constant contact brute-force styles for hero
 */

.hero.-newsletters {
  .ctct-form-defaults {
    background-color: transparent !important;
    padding: 40px 0 0 !important;
  }

  .ctct-form-header,
  .ctct-form-text {
    display: none !important;
  }

  .ctct-form-success {
    text-align: center !important;

    .ctct-form-header,
    .ctct-form-text {
      display: block !important;
    }
  }

  .ctct-form-custom {
    position: relative !important;
  }

  .ctct-form-field {
    margin: 0 0 20px !important;
    position: relative !important;
  }

  .ctct-form-label {
    font-size: 1.6rem !important;
    margin: 0 !important;
  }

  .ctct-form-required {
    &::before {
      display: none !important;
    }

    &::after {
      color: $color--fire;
      content: ' *';
    }
  }

  .ctct-form-element {
    background-color: $color--white !important;
    border: none !important;
    border-radius: 2px 0 0 2px !important;
    box-shadow: inset 1px 2px 3px rgba($color--black, 0.1) !important;
    font-size: 16px !important;
    height: 40px !important;
    padding: 0 10px !important;
    width: calc(100% - 100px) !important;

    &.is-error {
      box-shadow: 0 0 0 3px $color--fire !important;
    }

    @include respond-to('small') {
      border-radius: 2px !important;
      padding: 0 20px !important;
      width: calc(78% - 18px) !important;
    }
  }

  .ctct-form-button {
    background-color: $color--rose-of-sharon !important;
    border: none !important;
    border-radius: 0 2px 2px 0 !important;
    border-radius: 2px !important;
    font-size: 1.4rem !important;
    font-weight: 600 !important;
    line-height: 40px !important;
    padding: 0 !important;
    position: absolute !important;
    right: 0 !important;
    top: 0 !important;
    width: 100px !important;

    @include respond-to('small') {
      border-radius: 2px !important;
      width: 22% !important;
    }
  }

  .ctct-form-element,
  .ctct-form-button {
    &:focus {
      outline: none !important;
    }

    [data-whatintent='keyboard'] &:focus {
      box-shadow: 0 0 0 3px $color--yellow-sea !important;
      outline: none !important;
    }
  }

  [label-is] {
    .ctct-form-label {
      color: $color--venice-blue !important;
      font-size: 1.3rem !important;
      left: 10px !important;
      line-height: 40px !important;
      position: absolute !important;
      top: 0 !important;

      @include respond-to('small') {
        left: 20px !important;
      }
    }
  }

  [label-is='floated'] {
    .ctct-form-label {
      color: $color--tuatara !important;
      left: -9999rem !important;

      @include respond-to('small') {
        left: -9999rem !important;
      }
    }
  }

  .ctct-gdpr-text {
    color: $color--tuatara !important;
    font-size: 1.1rem !important;
    margin: 0 !important;
    text-align: center !important;
  }

  .ctct-form-errorMessage {
    font-weight: 700 !important;
    left: 0 !important;
    line-height: 40px !important;
    position: absolute !important;
    top: -35px !important;
  }

  .ctct-form-footer {
    display: none !important;
  }
}
