/*
 * media queries
 * small:         30em
 * medium:        48.75em
 * medium-large:  61.25em
 * large:         75em
 * x-large:       87.5em
 */
/*
 * constant contact brute-force styles for hero
 */
.hero.-newsletters .ctct-form-defaults {
  background-color: transparent !important;
  padding: 40px 0 0 !important; }

.hero.-newsletters .ctct-form-header,
.hero.-newsletters .ctct-form-text {
  display: none !important; }

.hero.-newsletters .ctct-form-success {
  text-align: center !important; }
  .hero.-newsletters .ctct-form-success .ctct-form-header,
  .hero.-newsletters .ctct-form-success .ctct-form-text {
    display: block !important; }

.hero.-newsletters .ctct-form-custom {
  position: relative !important; }

.hero.-newsletters .ctct-form-field {
  margin: 0 0 20px !important;
  position: relative !important; }

.hero.-newsletters .ctct-form-label {
  font-size: 1.6rem !important;
  margin: 0 !important; }

.hero.-newsletters .ctct-form-required::before {
  display: none !important; }

.hero.-newsletters .ctct-form-required::after {
  color: #bd3900;
  content: ' *'; }

.hero.-newsletters .ctct-form-element {
  background-color: #fff !important;
  border: none !important;
  border-radius: 2px 0 0 2px !important;
  box-shadow: inset 1px 2px 3px rgba(0, 0, 0, 0.1) !important;
  font-size: 16px !important;
  height: 40px !important;
  padding: 0 10px !important;
  width: calc(100% - 100px) !important; }
  .hero.-newsletters .ctct-form-element.is-error {
    box-shadow: 0 0 0 3px #bd3900 !important; }
  @media (min-width: 30em) {
    .hero.-newsletters .ctct-form-element {
      border-radius: 2px !important;
      padding: 0 20px !important;
      width: calc(78% - 18px) !important; } }

.hero.-newsletters .ctct-form-button {
  background-color: #c25700 !important;
  border: none !important;
  border-radius: 0 2px 2px 0 !important;
  border-radius: 2px !important;
  font-size: 1.4rem !important;
  font-weight: 600 !important;
  line-height: 40px !important;
  padding: 0 !important;
  position: absolute !important;
  right: 0 !important;
  top: 0 !important;
  width: 100px !important; }
  @media (min-width: 30em) {
    .hero.-newsletters .ctct-form-button {
      border-radius: 2px !important;
      width: 22% !important; } }

.hero.-newsletters .ctct-form-element:focus,
.hero.-newsletters .ctct-form-button:focus {
  outline: none !important; }

[data-whatintent='keyboard'] .hero.-newsletters .ctct-form-element:focus, [data-whatintent='keyboard']
.hero.-newsletters .ctct-form-button:focus {
  box-shadow: 0 0 0 3px #f7a505 !important;
  outline: none !important; }

.hero.-newsletters [label-is] .ctct-form-label {
  color: #045c78 !important;
  font-size: 1.3rem !important;
  left: 10px !important;
  line-height: 40px !important;
  position: absolute !important;
  top: 0 !important; }
  @media (min-width: 30em) {
    .hero.-newsletters [label-is] .ctct-form-label {
      left: 20px !important; } }

.hero.-newsletters [label-is='floated'] .ctct-form-label {
  color: #2f2f29 !important;
  left: -9999rem !important; }
  @media (min-width: 30em) {
    .hero.-newsletters [label-is='floated'] .ctct-form-label {
      left: -9999rem !important; } }

.hero.-newsletters .ctct-gdpr-text {
  color: #2f2f29 !important;
  font-size: 1.1rem !important;
  margin: 0 !important;
  text-align: center !important; }

.hero.-newsletters .ctct-form-errorMessage {
  font-weight: 700 !important;
  left: 0 !important;
  line-height: 40px !important;
  position: absolute !important;
  top: -35px !important; }

.hero.-newsletters .ctct-form-footer {
  display: none !important; }
